.footer {
  min-height: 203px;
  background-color: #161dbe;
  padding: 60px 0;
  &-content {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    @media(max-width: 767px)  {
      flex-wrap: wrap;
    }
  }
  &-social {
    display: flex;
    justify-content: flex-end;
    &-mob {
      display: none;
      @media(max-width: 632px)  {
        display: flex;
      }
    }
    @media(max-width: 992px) {
      justify-content: flex-start;
    }
    @media(max-width: 632px)  {
      display: none;
    }
    &__item {
      margin-left: 40px;
      @media(max-width: 992px) {
        margin-left: 20px;
        &:first-child {
          margin-left: 0;
        }
      }
    }
    &__link {
      font-size: 20px;
      color: #fff;
      &-telegram:hover {
        color: #e9ecef;
      }
      &-whatsapp:hover {
        color: forestgreen;
      }
    }
  }
  &-logo {
    color: #fff;
    font-size: 15px;
    font-weight: 900;
    text-transform: uppercase;
  }
}
.footer-nav {
  display: flex;
  @media(max-width: 992px) {
    flex-wrap: wrap;
  }
  &__item {
    margin-left: 40px;
    @media(max-width: 992px) {
      width: 100%;
      margin-bottom: 20px;
      margin-left: 0;
    }
    &:first-child {
      margin-left: 0;
    }
  }
  &__link {
    color: #fff;
    font-size: 16px;
    font-weight: 400;
  }
}

.footer-tel {
  display: flex;
  justify-content: flex-end;
  color: #fff;
  font-size: 19px;
  font-weight: 500;
}
