.about {
  padding: 60px 0;
  &__title {
    text-transform: uppercase;
    font-size: 24px;
    text-align: center;
    margin-bottom: 20px;
    @media(max-width: 768px) {
      font-size: 19px;
    }
  }
  &__content {
    margin-bottom: 80px;
    @media(max-width: 1024px) {
      margin-bottom: 20px;
    }
    p {
      font-size: 20px;
      margin-bottom: 20px;
    }
  }
  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 382px;
    height: 61px;
    border: 2px solid #161dbe;
    background-color: #161dbe;
    color: #fff;
    text-align: center;
    &:hover {
      background-color: #fff;
      color: inherit;
    }
    @media(max-width: 420px) {
      width: 100%;
    }
  }
}
