.direction-activity {
  display: flex;
  justify-content: space-between;
  @media(max-width: 1280px) {
    flex-wrap: wrap;
    justify-content: center;
  }
  &-item {
    position: relative;
    display: flex;
    width: 20%;
    cursor: pointer;
    overflow: hidden;
    margin-right: 3px;
    //&:nth-child(4n) {
    //  margin-right: 0;
    //  @media(max-width: 960px) {
    //    max-width: 100%;
    //    width: 100%;
    //    margin: 10px;
    //  }
    //}
    //&:last-child {
    //  margin-right: 0;
    //  @media(max-width: 960px) {
    //    max-width: 100%;
    //    width: 100%;
    //    margin: 10px;
    //  }
    //}
    @media(max-width: 1280px) {
      width: 48%;
      max-width: 300px;
      height: 400px;
      margin: 10px;
    }
    @media(max-width: 960px) {
      max-width: 100%;
      width: 100%;
      margin: 10px;
    }
  }
  &-link {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    img {
      @media(max-width: 1280px) {
        width: 100%;
        height: auto;
        position: absolute;
        left: -9999px;
        right: -9999px;
        margin: 0 auto;
      }
    }
  }
  &-box-show {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    padding: 30px;
  }
  &-title {
    color: #ffffff;
    font-size: 26px;
    text-transform: uppercase;
    font-weight: 700;
  }
  &-subtitle {
    color: #ffffff;
    font-size: 26px;
    text-transform: uppercase;
    font-weight: 400;
    margin-bottom: 57px;
  }
  &-desc {
    color: #fff;
    font-size: 18px;
    font-weight: 400;
  }
  &-show-title {
    color: #ffffff;
    font-size: 23px;
    line-height: 21px;
    text-transform: uppercase;
    font-weight: 700;
  }
  &-show-subtitle {
    position: relative;
    color: #ffffff;
    font-size: 23px;
    text-transform: uppercase;
    font-weight: 400;
    z-index: 1;
    &:before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -70%;
      width: 100%;
      height: 50px;
      background: #161dbe;
      z-index: -1;
      opacity: .7;
    }
  }
}

.direction-activity-content {
  position: absolute;
  top: 100%;
  left: 0;
  height: 100%;
  width: 100%;
  transition: all .5s;
  padding: 30px;
  background: #161dbe;
  z-index: 2;
}
.direction-activity-content__link {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 150px;
  width: calc(100% - 60px);
  position: absolute;
  overflow: hidden;
  left: 50%;
  transform: translateX(-50%);
  bottom: 30px;
  border: 1px solid #fff;
  padding: 15px;
  color: #fff;
  font-size: 12px;
  font-weight: 700;
  transition: all .3s ease-in-out;
  @media(max-width: 960px) and (min-width: 768px) {
    max-width: 180px;
  }
  &:after {
    background: #fff;
    content: "";
    height: 155px;
    left: -75px;
    opacity: .2;
    position: absolute;
    top: -50px;
    transform: rotate(35deg);
    transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
    width: 50px;
    z-index: -10;
  }
  &:hover {
    color: #fff;
    &:after {
      left: 120%;
      transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
    }
  }
}
.direction-activity-link:hover .direction-activity-content {
  top: 0;
}
